<template>
  <div class='homebanner' :style="{'background-image':'url('+bgpic+')'}"></div>
</template>
<script>
    export default{
        name:'homebanner',
        props:['bg_img'],
        data(){
            return {
                bgpic:''
            }
        },
        mounted(){
            //console.log(require("../assets"+this.bg_img));
            this.bgpic=require("../../assets"+this.bg_img);
        }
    }
</script>




