<template>
  <section class='childheader' :style="{'backgroundImage':'url('+childhead_pic+')'}"></section>
</template>
<script>
    export default{
        name:'ChildHeader',
        props:['childhead_bg'],
        data(){
            return {
                childhead_pic:''
            }
        },
        mounted(){
            this.childhead_pic=require('../../assets/'+this.childhead_bg);
        }
    }
</script>
<style scoped>
.childheader{
    width:100%;
    height:350px;
    background-repeat: no-repeat 50% 50%;
    /* background-image:url(../assets/img/order_header.jpg); */
    background-size:cover; 
}
</style>




