$(()=>{
    var pno,pCount,source;
    function requestData(pno,source){
        pno=pno || 1;
        source=source ||'menu';
        $.ajax({
            type:"GET",
            url:"data/order/"+source+".php?pno="+pno,
            dataType:'json',
            success:function(data){
                //console.log(data);
                var {pno,count,pageCount,data}=data;
                pCount=pageCount;
                var content='';
                //挂载主体内容
                for (var menu of data){
                    //console.log(data.length);
                    content += `<li>
                        <div class="food_box">
                            <div class='image_box'>
                                <img src="${menu.pic}" alt="">
                            </div>
                            <h4>${menu.title}</h4>
                            <p>${menu.subtitle}</p>
                            <h6>￥${menu.price}</h6>
                            <button class="btn btn-success food_select">选择</button>
                        </div>
                        </li>`;
                }
                $('.content_right ul').html(content);
                //挂载分页按钮
                content=`<a class='btn btn-default modify' href="prev">上一页</a>`;
                for(var i=1;i<=pageCount;i++){
                    if(i==pno){
                    content+=`
                    <a class='btn btn-default page pageActive' href="${i}">${i}</a>
                    `;
                    }else{
                    content+=`
                    <a class='btn btn-default page' href="${i}">${i}</a>
                    `;
                    }
                }
                content+=`<a class='btn btn-default modify' href="last">下一页</a>`;
                $('.order_content .paging').html(content);
            }
        });
    }
    requestData();
    /*菜单特效*/
    //on事件代理因为是ajax异步加载数据
    $('.content_right').on('click','.food_select',function(e){
        //console.log(1);
        var WIDTH=window.innerWidth;
        var HEIGHT=window.innerHeight;
        var x=e.clientX;
        var y=e.clientY;
        var timer=null;
        //console.log(x,y);console.log(WIDTH,HEIGHT);
        $html=$(`<div class='dots' style='position:fixed;top:${y}px;left:${x}px;width:15px;height:15px;border-radius:50%;background-color:purple;'></div>`);
        $html.appendTo('.content_right');
        if(timer){
            return ;
        }
        timer=setTimeout(()=>{
            $('.dots').css({'left':WIDTH-100,'top':HEIGHT-100});
            setTimeout(()=>{
                $('.dots').remove();
                timer=null;
            },500)
        },100);
    });
    //分页按钮事件
     $('.order_content').on('click',' .paging .page',function(e){
         e.preventDefault();
         pno=parseInt($(e.target).attr('href'));
         requestData(pno,source);
     })
     $('.order_content').on('click',' .paging .modify',function(e){
        e.preventDefault();
        if(!pno){
            pno=1;
        }
        //console.log(pno);console.log(pCount);
        if($(e.target).attr('href')=='prev'){
            if(pno>1){
                pno--;
                requestData(pno,source);
            }else{
                return;
            }
        }else{
            if(pno<pCount){
                pno++;
                requestData(pno,source);
            }else{
                return;
            }
        }
     });
     //菜单栏按钮
    $('.order_content .content_left ul li a').click(function(e){
        e.preventDefault();
        source=$(this).attr('href');
        requestData(1,source);
    });
    //头部菜单栏
    $('.order_content .navmenu_top a').click(function(e){
        e.preventDefault();
        source=$(this).attr('href');
        requestData(1,source);
    });
    //鼠标悬浮特效
    $('.content_right').on('mouseover','.food_box',function(){
        //console.log(1);
        $(this).children('.image_box').children('img').addClass('image_trans');
        $(this).parent().addClass('foodbox_active');
    });
    $('.content_right').on('mouseout','.food_box',function(){
        //console.log(1);
        $(this).children('.image_box').children('img').removeClass('image_trans');
        $(this).parent().removeClass('foodbox_active');
    })
});


