<template>
  <div class="navbar navbar-default navbar-fixed-top header_nav">
        <div class="container">
            <div class="navbar-header">
                <a href="#" class="navbar-brand my_logo"></a>
                <a href="#my-menu" class="navbar-toggle" data-toggle="collapse">
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
            </a>
            </div>
            <div id="my-menu" class="navbar-collapse navbar-right collapse">
                <ul class="nav navbar-nav">
                    <li><router-link to='/index'>主 页  Home</router-link></li>
                    <li><a href="#">今日特色  Spec</a></li>
                    <li><router-link to='/order'>点 餐  Order</router-link></li>
                    <li><a href="#">服 务  Server</a></li>
                    <li><router-link to='/about'>关于我们  About</router-link></li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    export default{
        name:'HomeHeader',
        data(){
            return {
                screenWidth:0,
                name:'homeheader'
            }
        },
        mounted(){
                $(window).scroll(function(){
                    var scorllTop=document.body.scrollTop || document.documentElement.scrollTop;
                    console.log(scorllTop);
                    if(scorllTop==0){
                        $('.header_nav').removeClass('header_nav_scorll');
                        $(".header_nav .my_logo").css('backgroundPosition',' 0 0');
                        $('.navbar-nav li a').css("color",'rgba(189,189,189)');
                    }else{
                        $('.header_nav').addClass('header_nav_scorll');
                        $(".header_nav .my_logo").css('backgroundPosition',' 0 -40px');
                        $('.navbar-nav li a').css("color",'rgba(0,0,0,.7)');
                    }
                })
        }
    }
</script>
<style scoped>
.header_nav .my_logo{
    width: 117px;
    height: 40px;
    position: relative;
    background-image: url(../../assets/img/logo.png);
    transition: all .2s ease;
}
@media screen and (min-width:1170px){
    .header_nav{
        background-color:transparent;
        border:0;
    }
    .header_nav #my-menu ul li a{
        color:white;
        padding:40px 0;
        margin:0 15px;
        text-align: center;
    }
    .header_nav .my_logo{
        top: 40px;
    }

}
@media (max-width:1170px) and (min-width:768px){
    .header_nav{
        background-color:transparent;
        border:0;
    }
    .header_nav #my-menu ul li a{
        color:white;
        padding:40px 0;
        margin:0 15px;
        text-align: center;
    }
    .header_nav .my_logo{
        top: 40px;
    }
}
@media screen and (max-width:768px){
    .header_nav{
        background-color:fff;
    }
    .header_nav .navbar-collapse ul li a{
        color:rgba(0,0,0,.7);
    }
    .header_nav .navbar-toggle{
        border:0;
    }
    .header_nav .my_logo{
        top:6px;
        left:15px;
        background-image: 0 -40px;
    }
}
.header_nav_scorll{
    background-color: #fff;
}
</style>




