                <div class="carousel-caption">
                    <h1>Welcome Resto Restaurant</h1>
                    <p>欢迎来到Resto餐厅</p>
                    <h6><b>“</b>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Ratione in vel quis eum nihil consequuntur sapiente quo aperiam velit repellat. Quisquam, fuga. Quae velit nam ratione repellat! Consectetur, pariatur ducimus.<b>”</b></h6>
