        <a class="right carousel-control" href="#myCarousel" role="button" data-slide="next">
            <span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
            <span class="sr-only">Next</span>
        </a>
    </div>
</template>
<script>
    export default {
        name:'HomeCarousel',
        methods:{
            textEffect(){
                setTimeout(()=>{
                    $('#myCarousel .carousel-inner .item .carousel-caption').removeClass('textactive');
                    $('#myCarousel .carousel-inner .active .carousel-caption').addClass('textactive');
                })
            }
        },
        mounted(){
            this.textEffect();
            $('#myCarousel').on('slid.bs.carousel', ()=> {
                this.textEffect();
            });

        }
    }
</script>
<style scoped>
.carousel img{
    width:100%;
    height:50%;
}
.carousel-caption{
    top:20%;
    left:10%;
}
.carousel-caption h1{
    font-family:"French Script Mt",cursive;
}
.carousel-caption h6{
    text-align: left;
    margin:auto;
    font-weight: normal;
    letter-spacing:.1em;
}
.carousel-caption h6 b{
    font-size:2em;
    color:#d9534f;
}
.carousel-caption a{
    margin:2em 0;
}
@media screen and (min-width:1170px){
    .carousel-caption h1{
        font-size:5em;
    }
    .carousel-caption p{
        font-size:3em;
    }
    .carousel-caption h6{
        width:50%;
        font-size:1em;
    }
}
@media (max-width:1170px) and (min-width:768px){
    .carousel-caption h1{
        font-size:4em;
    }
    .carousel-caption p{
    font-size:2em;
    }
    .carousel-caption h6{
        width:60%;
        font-size:.8em;
    }
}
@media screen and (max-width:768px){
    .carousel-caption h1{
        font-size:3em;
    }
    .carousel-caption p{
    font-size:1.5em;
    }
    .carousel-caption h6{
        display: none;
    }
}
#myCarousel .carousel-inner .item .carousel-caption{
    transition: all 1.5s ease;
    opacity: 0;
}
#myCarousel .carousel-inner .item .textactive{
    top:15%;
    opacity: 1;
}
</style>



