import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Order from '@/components/Order'
import About from '@/components/About'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '/',
      component: Index
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/order',
      name: 'order',
      component: Order
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
  ]
})



