$(()=>{
    $.ajax({
        type:'GET',
        url:'footer.html',
        success:function(data){
            $('#footer').html(data);
        }
    })
    /*头部颜色*/
    $(window).scroll(function(){
        var scorllTop=document.body.scrollTop || document.documentElement.scrollTop;
        //console.log(scorllTop);
        if(scorllTop==0){
            $('.header_nav').removeClass('header_nav_scorll');
            $(".header_nav .my_logo").css('backgroundPosition',' 0 0');
            $('.navbar-nav li a').css("color",'white');
        }else{
            $('.header_nav').addClass('header_nav_scorll');
            $(".header_nav .my_logo").css('background-position',' 0 -40px');
            $('.navbar-nav li a').css("color",'rgba(0,0,0,.7)');
        }
    })
    /*时间*/
    $('#datetimepicker2').datetimepicker({
        datepicker:false,
        format:'H:i',
        step:5
    });
    $('#datetimepicker1').datetimepicker({
        lang:'ch',
        timepicker:false,
        format:'Y-m-d',
        formatDate:'Y/m/d',
        minDate:'-1970/01/01', // yesterday is minimum date
        maxDate:'+1970/01/07' // and tommorow is maximum date calendar
    });
});


