<script>
    import ChildHeader from './child/ChildHeader';
    import ChildNav from './child/ChildNav';
    import Footer from './Footer';
    import '../assets/css/order.css';
    //import '../assets/js/order.js';
    export default {
        components:{ChildHeader,ChildNav,Footer},
        data(){
            return{
                pno:0,
                pCount:0,
                source:'menu',
                data:[]
            }
        },
        methods:{
            requestData(pno,source){
                pno=pno || 1;
                source=source ||'menu';
                this.$http.get('http://localhost/2018-5-10/tpls/src/assets/data/order/'+source+'.php?pno='+pno).then((data)=>{
                        console.log(data.body);
                        var {pno,count,pageCount,data}=data.body;
                        
                        this.pCount=pageCount;
                        var content='';
                        for(var i in data){
                            data[i].pic=require('../assets/'+data[i].pic);
                        }
                        console.log(data);
                        this.data=data;
                        /*//挂载主体内容
                        for (var menu of data){
                            //console.log(data.length);
                            content += `<li>
                                <div class="food_box">
                                    <div class='image_box'>
                                        <img src="${menu.pic}" alt="">
                                    </div>
                                    <h4>${menu.title}</h4>
                                    <p>${menu.subtitle}</p>
                                    <h6>￥${menu.price}</h6>
                                    <button class="btn btn-success food_select">选择</button>
                                </div>
                                </li>`;
                        }
                        //$('.content_right ul').html(content);
                        //挂载分页按钮
                        content=`<a class='btn btn-default modify' href="prev">上一页</a>`;
                        for(var i=1;i<=pageCount;i++){
                            if(i==pno){
                            content+=`
                            <a class='btn btn-default page pageActive' href="${i}">${i}</a>
                            `;
                            }else{
                            content+=`
                            <a class='btn btn-default page' href="${i}">${i}</a>
                            `;
                            }
                        }
                        content+=`<a class='btn btn-default modify' href="last">下一页</a>`;
                        //$('.order_content .paging').html(content);*/
                })
            },
            sendAjax(msg){
                //console.log(msg);
                //this.source=$(e.target).attr('href');
                this.source=msg;
                this.requestData(1,this.source);
            }
        },
        mounted(){
            this.requestData();
                $('.content_right').on('click','.food_select',function(e){
                //console.log(1);
                var WIDTH=window.innerWidth;
                var HEIGHT=window.innerHeight;
                var x=e.clientX;
                var y=e.clientY;
                var timer=null;
                var $html='';
                //console.log(x,y);console.log(WIDTH,HEIGHT);
                $html=$(`<div class='dots' style='position:fixed;top:${y}px;left:${x}px;width:15px;height:15px;border-radius:50%;background-color:purple;'></div>`);
                $html.appendTo('.content_right');
                if(timer){
                    return ;
                }
                timer=setTimeout(()=>{
                    $('.dots').css({'left':WIDTH-100,'top':HEIGHT-100});
                    setTimeout(()=>{
                        $('.dots').remove();
                        timer=null;
                    },500)
                },100);
            });
                //分页按钮事件
            $('.order_content').on('click',' .paging .page',(e)=>{
                e.preventDefault();
                this.pno=parseInt($(e.target).attr('href'));
                console.log(this.pno);
                this.requestData(this.pno,this.source);
            })
            $('.order_content').on('click',' .paging .modify',(e)=>{
                e.preventDefault();
                if(!this.pno){
                    this.pno=1;
                }
                //console.log(pno);console.log(pCount);
                if($(e.target).attr('href')=='prev'){
                    if(this.pno>1){
                        this.pno--;
                        this.requestData(this.pno,this.source);
                    }else{
                        return;
                    }
                }else{
                    if(this.pno<this.pCount){
                        this.pno++;
                        this.requestData(this.pno,this.source);
                    }else{
                        return;
                    }
                }
            });
            //菜单栏按钮
            // $('.order_content .content_left ul li a').click((e)=>{
            //     e.preventDefault();
                
            //     this.source=$(e.target).attr('href');
            //     console.log(e.target);
            //     this.requestData(1,this.source);
            // });
            //头部菜单栏
            // $('.order_content .navmenu_top a').click((e)=>{
            //     e.preventDefault();
            //     this.source=$(e.target).attr('href');
            //     this.requestData(1,this.source);
            // });
            //鼠标悬浮特效
            $('.content_right').on('mouseover','.food_box',function(){
                //console.log(1);
                $(this).children('.image_box').children('img').addClass('image_trans');
                $(this).parent().addClass('foodbox_active');
            });
            $('.content_right').on('mouseout','.food_box',function(){
                //console.log(1);
                $(this).children('.image_box').children('img').removeClass('image_trans');
                $(this).parent().removeClass('foodbox_active');
            })
        }
    };
</script>
<style scoped>
.nav>li>a{
    padding:15px 0 0 0;
}
.content_left ul li a{
    padding:0 10px 0 10px;
}
.content_left ul li>.thisActive{
    border-right:5px solid black;
}
</style>




